.header{padding:20px 0 20px;position:relative}@media only screen and (max-width: 767px){.header{padding:20px 0 0}}.header .inner_{display:flex;justify-content:space-between;align-items:center}@media only screen and (max-width: 767px){.header .inner_{flex-wrap:wrap}}.header .inner_>div{display:flex;align-items:center;gap:20px}.header .inner_ .logo{width:150px}.header .inner_ .logo a{cursor:pointer}.header .inner_ .navigation ul{display:flex;gap:20px}.header .inner_ .navigation ul li{font-weight:700}.header .inner_ .navigation ul li.active{color:#f083b3}.header .inner_ .right_sec{flex:1;justify-content:flex-end}.header .inner_ .right_sec .srch_bar{width:350px;position:relative}@media only screen and (max-width: 767px){.header .inner_ .right_sec .srch_bar{min-width:100%}}.header .inner_ .right_sec .srch_bar input{width:100%}.header .inner_ .right_sec .srch_bar button{position:absolute;right:20px;top:50%;transform:translateY(-50%)}.header .inner_ .right_sec .pro_cart{display:flex;gap:30px;align-items:center}@media only screen and (max-width: 767px){.header .inner_ .right_sec .pro_cart{position:absolute;right:20px;top:50%;z-index:3;transform:translateY(-50%)}}.header .inner_ .right_sec .cart{display:flex;position:relative;cursor:pointer;width:24px;height:24px}.header .inner_ .right_sec .cart .count{position:absolute;bottom:-5px;right:-10px;width:18px;height:18px;background-color:#f083b3;color:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;font-size:12px}.header .inner_ .right_sec .profile{display:flex;align-items:center;gap:10px;cursor:pointer;position:relative}